<template>
  <svg
    :viewBox="viewBox"
    :width="width"
    :height="height"
    :fill="fill"
    :stroke="stroke"
  />
</template>

<script>
import { parse } from 'postsvg'
import render from 'posthtml-render'

//   
const cache = new Map()

export default {
  name: 'BaseSVGIcon',
  props: {
    //   ( )
    name: {
      type: String,
      required: true
    },

    // 
    width: {
      type: [Number, String],
      default: 18
    },

    // 
    height: {
      type: [Number, String],
      default: 18
    },

    //  
    fill: {
      type: String,
      default: 'currentColor'
    },

    //  
    stroke: {
      type: String,
      default: 'none'
    },

    //    ready 
    generateReadyEvent: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    //    SVG-
    svgString: ''
  }),
  computed: {
    //   SVG-
    filepath() {
      return require(`@/assets/icons/${this.name}.svg`)
    },

    //   SVG-
    parsedSVG() {
      return this.svgString ? parse(this.svgString) : null
    },

    //  
    viewBox() {
      return this.parsedSVG ? this.parsedSVG.root.attrs.viewBox : '0 0 20 20'
    }
  },
  watch: {
    //   ,  
    filepath: {
      immediate: true,
      handler: 'loadFile'
    },

    //      SVG
    svgString: 'refreshSvg',

    //     SVG
    stroke: 'refreshSvg',
    fill: 'refreshSvg'
  },
  methods: {
    //   SVG 
    loadFile() {
      this.getSvgIconText().then(
        responseText => (this.svgString = responseText)
      )
    },

    //  SVG-    
    // +    
    async getSvgIconText() {
      const url = this.filepath

      //   
      //     
      if (!cache.has(url)) {
        try {
          cache.set(
            url,
            fetch(url).then(r => r.text())
          )
        } catch (e) {
          cache.delete(url)
        }
      }

      //    
      return cache.has(url)
        ? await cache.get(url)
        : Promise.reject(new Error(' SVG-   '))
    },

    //  SVG
    refreshSvg() {
      Promise.resolve(this.parsedSVG)
        //  
        .then(svgTree => {
          svgTree.each('path', node => (node.attrs.fill = this.fill))
          return svgTree
        })
        //     <svg>
        .then(svgTree => render(svgTree.root.content))
        //    <svg> 
        .then(svgHtml => (this.$el.innerHTML = svgHtml))
        .then(() => this.generateReadyEvent && this.$emit('ready'))
        .catch(error => {
          this.$emit('error', error)
        })
    }
  }
}
</script>

<style>
svg {
  display: inline-block;
  vertical-align: baseline;
  margin-bottom: -2px;
  transition: fill 0.3s, stroke 0.3s;
}
</style>
